.app-container {
  width: 400px;
  height: 400px;
}

.calculator {
  display: grid;
  grid-template-columns: repeat(8, 50px);
}

.display {
  grid-column-start: 1;
  grid-column-end: 9;
  height: 80px;
  font-size: 30px;
  text-align: right;
  border: 1px solid black;
  padding-right: 10px;
  padding-top: 5px;
  background-color: #0000aa;
  color: white;
}

button {
  height: 50px;
  color: black;
  background-color: white;
  border: 1px solid lightgray;
}

button.operator {
  background-color: #5555ff;
  color: white;
}

button.operator:hover {
  color: white;
  /* background-color: #00ce00; */
  background-color: #4343ff;
  transition: background-color 0.1s linear;
}

button.igual:hover {
  color: rgb(0, 0, 0);
  background-color: #7FFFD4;
  transition: background-color 0.1s linear;
}

button.clear:hover {
  color: rgb(0, 0, 0);
  background-color: #FF4500;
  transition: background-color 0.1s linear;
}